.actus-board-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    overflow: scroll;

    :root {
        --body: #fafafa;
        --text-default: #1d1d1f;
        --text-secondary: #86868b;

        @media (prefers-color-scheme: light) {
            --body: #fafafa;
            --text-default: #1d1d1f;
        }

        @media (prefers-color-scheme: dark) {
            --body: #161616;
            --text-default: #f5f5f7;
        }
    }

    .header {
        height: 10%;
        width: 100%;
        padding: 4vh;

        h1 {
            font-size: 5vh;
            color: $purple-1;
            font-family: $sofiaMedium;
        }
    }

    .actus-container {
        height: 90%;
        width: 100%;
        padding: 2vh;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: wrap;

        h1 {
            font-family: $sofiaSemiBold;
            color: $purple-1;
        }

        .premium-actus {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-evenly;
            margin: 2vh 0;

            .event-add-element {
                height: 45vh;
                width: 17%;
                border-radius: 3vh;
                box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
                background-color: $blue-1;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 1vh 1vh;
                cursor: pointer;


                img {
                    width: 50%;
                    filter: brightness(1) invert(1);
                }
            }

            .event-element {
                height: 45vh;
                width: 17%;
                margin: 1vh 1vh;
                background-color: $white;
                border-radius: 3vh;
                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                overflow: hidden;

                .event-infos {
                    height: 85%;
                    width: 100%;

                    .event-infos-image {
                        height: 60%;
                        width: 100%;
                        border-radius: 3vh;
                        overflow: hidden;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }

                    .event-infos-name {
                        padding: 1vh;

                        h4 {
                            font-size: 2.1vh;
                            font-weight: 800;
                        }

                        h6 {
                            font-size: 1.4vh;
                            margin: 1vh 0;
                        }
                    }
                }

                .event-element-footer {
                    width: 100%;
                    height: 15%;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    padding: 1vh;

                    .footer-view {
                        width: 5vh;
                        height: 5vh;
                        margin-right: 1vh;
                        border-radius: 100%;
                        background-color: $blue-1;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;

                        img {
                            width: 60%;
                            filter: brightness(1) invert(1);
                        }
                    }

                    .footer-edit {
                        width: 5vh;
                        height: 5vh;
                        margin-right: 1vh;
                        border-radius: 100%;
                        background-color: $blue-1;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;

                        img {
                            width: 60%;
                            filter: brightness(1) invert(1);
                        }
                    }

                    .footer-delete {
                        width: 5vh;
                        height: 5vh;
                        border-radius: 100%;
                        background-color: $red;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;

                        img {
                            width: 60%;
                            filter: brightness(1) invert(1);
                        }

                    }
                }
            }
        }

        .others-actus {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            margin: 2vh 0;
            justify-content: space-evenly;

            .event-add-element {
