                width: 17%;
                border-radius: 3vh;
                box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
                background-color: $blue-1;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 1vh 1vh;
                cursor: pointer;


